.App {
  height: 100vh; 
  background-image: url(./images/vista.jpg);
  background-size: cover;
  font-family: 'Titillium Web', sans-serif;
}

.App-players {
  height: 100%;
  display: -ms-flexbox;
  display: flex;
}

@media only screen and (max-width: 720px)  {
  .App-players {
    -ms-flex-direction: column;
        flex-direction: column;
  }  
}

.App-controls {
  position: fixed;
  bottom: 3%;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}

.App-controls span {
  padding: 1em;
  background-color: hsla(120, 2%, 20%, .8);
  border-radius: 2px;
  font-size: 1.1em;
  color: hsla(81, 28%, 90%, 1);
}

.App-controls span:hover {
  background-color: hsla(120, 2%, 20%, .9);
  cursor: pointer;
}

@media only screen and (max-width: 720px)  {
  .App-controls {
    left: auto;
    bottom: 50%;
    right: 0;
    -webkit-transform-origin: bottom right;
        -ms-transform-origin: bottom right;
            transform-origin: bottom right;
    -webkit-transform: translateX(-13%) rotate(-90deg) translateX(50%);
        -ms-transform: translateX(-13%) rotate(-90deg) translateX(50%);
            transform: translateX(-13%) rotate(-90deg) translateX(50%);
  } 
}



